.text-center {
    text-align: center;
}

.img-placeholder {
    width: 100%;
}

.img-pillar {
    max-width: 125px;
}

/* COLUMN SIZING */
.column {
    padding: 0.75em;
}

.basis10 {
    width: 10%;
}

.basis30 {
    width: 30%;
}

.basis40 {
    width: 40%;
}

.basis50 {
    width: 50%;
}

.basis60 {
    width: 60%;
}

.basis70 {
    width: 70%;
}

.basis90 {
    width: 90%;
}

/* CONTAINER SIZING */
.container-cc {
    width: 85%;
    max-width: 66em;
    margin: 0 auto;
}

/* FLEXBOX STUFF <-- (is this helpful?)*/
.flex {
    display: flex;
}

.flex-direction-column {
    display: flex;
    flex-direction: column;
}

.flex-direction-row-reverse {
    display: flex;
    flex-direction: row-reverse;
}

.centercenter {
    display: flex;
    justify-content: center;
    align-items: flex-start;
}

.flex-space-between {
    display: flex;
    justify-content: space-between;
}

