/* COLORS */
:root {
    --custom: rgb(122, 140, 31);

    --bgMain: var(--darkGreen);
    --accent: var(--darkGreen);
    --accentBG: var(--darkGreen);

    --darkBlue: #006f74;
    --brightBlue: #00a3b4;
    --darkGreen: #575e22;
    --brightGreen: #8c9b28;
    --darkRed: #951a1d;
    --brightRed: #d52327;
    --darkPurple: #662547;
    --brightPurple: #9b0e5b;
    --orange: #9b621c;
    --darkOrange: #c14e27;
    --brightOrange: #ec621d;
    --darkYellow: #d8a739;
    --brightYellow: #ffcd5e;
    --pink: #dd4e4e;
    --brightPink: #e3635f;
    --darkPink: #c04343;

    --white: #fff;
    --lightGray: #f2f2f2;
    --darkGray: #a3a3a3;
    --ink: #2c2c2c;
    --caption: hsl(0, 0%, 40%);
    --black: #000000;

    /* layout */

    --section-padding: 2em;
    --module-padding: 3em;
    --big-module-padding: 4em;
}
.text-white {
    color: var(--white);
}

.bg-white {
    background-color: var(--white);
}

.bg-lightGray {
    background-color: var(--lightGray);
}

.bg-darkRed {
    background-color: var(--darkRed);
