}

/* DIVIDER - can be done with an HR element?*/

.border-sandwich {
    border-top: 1px solid gray;
    border-bottom: 1px solid gray;
}

.border-top {
    border-top: 1px solid gray;
}
.border-bottom {
    border-bottom: 1px solid gray;
}

/* LAYOUT - for what? */

.split {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-items: center;
    justify-content: center;
    /*justify-content: space-between;*/
}

@media (min-width: 1400px) {
    :root {
        --section-padding: 4em;
        --module-padding: 6em;
    }
}
