.imgTestimonial {
    width: 100px;
}

.quote {
    max-width: 45px;
    margin-right: 15px;
}

.video-container {
    position: relative;
    padding-bottom: 56.25%; /* 16:9 aspect ratio (height / width = 9 / 16 * 100) */
    height: 0;
    overflow: hidden;
}

.video-container iframe {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

#RecordingTestimonial {
    padding-block: var(--module-padding);

    .thanksStudent {
        margin-top: 1em;
    }
}
